	/* Button number 3 css here  */

	.button-3 {
		color: #000;
		background-color: transparent;
		border: 1px solid #266DB6;
		position: relative;
		touch-action: manipulation; 
	}
	  
	  .button-3:hover,
	  .button-3:active {
		outline: 0;
	  }
	  
	  .button-3:hover {
		background-color: transparent;
	  }
	  
	  .button-3:before {
		background-color: #D5EDF6;
		content: "";
		height: calc(100% + 3px);
		position: absolute;
		right: -7px;
		top: -9px;
		transition: background-color 300ms ease-in;
		width: 100%;
		z-index: -1;
	  }
	  
	  .button-3:hover:before {
		background-color: #6DCFF6;
	  }
	
	  /* Button number 4  */
	
	  .button-4 {
		outline: 0;
		border: 1px solid black;
		position: relative;
	  }
	  
	  .button-4:after {
		content: "";
		background-color: #ffe54c;
		width: 100%;
		z-index: -1;
		position: absolute;
		height: 100%;
		top: 7px;
		left: 7px;
		transition: 0.2s;
	  }
	  
	  .button-4:hover:after {
		top: 0px;
		left: 0px;
	  }
	
	
	
	  /* Button number 5  */
	
	.button-5,
	.button-5:after {
	  background: linear-gradient(45deg, transparent 5%, #FF013C 5%);
	  box-shadow: 6px 0px 0px #00E6F6;
	  outline: transparent;
	  position: relative;
	}
	
	.button-5:after {
	  --slice-0: inset(50% 50% 50% 50%);
	  --slice-1: inset(80% -6px 0 0);
	  --slice-2: inset(50% -6px 30% 0);
	  --slice-3: inset(10% -6px 85% 0);
	  --slice-4: inset(40% -6px 43% 0);
	  --slice-5: inset(80% -6px 5% 0);
	  
	  content: 'ALTERNATE TEXT';
	  display: block;
	  position: absolute;
	  top: 0;
	  left: 0;
	  right: 0;
	  bottom: 0;
	  background: linear-gradient(45deg, transparent 3%, #00E6F6 3%, #00E6F6 5%, #FF013C 5%);
	  text-shadow: -3px -3px 0px #F8F005, 3px 3px 0px #00E6F6;
	  clip-path: var(--slice-0);
	}
	
	.button-5:hover:after {
	  animation: 1s glitch;
	  animation-timing-function: steps(2, end);
	}
	
	@keyframes glitch {
	  0% {
		clip-path: var(--slice-1);
		transform: translate(-20px, -10px);
	  }
	  10% {
		clip-path: var(--slice-3);
		transform: translate(10px, 10px);
	  }
	  20% {
		clip-path: var(--slice-1);
		transform: translate(-10px, 10px);
	  }
	  30% {
		clip-path: var(--slice-3);
		transform: translate(0px, 5px);
	  }
	  40% {
		clip-path: var(--slice-2);
		transform: translate(-5px, 0px);
	  }
	  50% {
		clip-path: var(--slice-3);
		transform: translate(5px, 0px);
	  }
	  60% {
		clip-path: var(--slice-4);
		transform: translate(5px, 10px);
	  }
	  70% {
		clip-path: var(--slice-2);
		transform: translate(-10px, 10px);
	  }
	  80% {
		clip-path: var(--slice-5);
		transform: translate(20px, -10px);
	  }
	  90% {
		clip-path: var(--slice-1);
		transform: translate(-10px, 0px);
	  }
	  100% {
		clip-path: var(--slice-1);
		transform: translate(0);
	  }
	}
	
	  /* Button number 6  */
	
	.button-6 {
		background-color: #3DD1E7;
		border: 0 solid #E5E7EB;
		box-sizing: border-box;
		color: #000000;
		display: flex;
	
		justify-content: center;
		position: relative;
		text-align: center;
		text-decoration: none #000000 solid;
		text-decoration-thickness: auto;
		/* width: 100%;
		max-width: 460px; */
		position: relative;
		transform: rotate(-2deg);
	  }
	  
	  .button-6:focus {
		outline: 0;
	  }
	  
	  .button-6:after {
		content: '';
		position: absolute;
		border: 1px solid #000000;
		bottom: 4px;
		left: 4px;
		width: calc(100% - 1px);
		height: calc(100% - 1px);
	  }
	  
	  .button-6:hover:after {
		bottom: 2px;
		left: 2px;
	  }
	
	  /* Button number 7  */
	
	  .button-7 {
		align-self: center;
		background-color: #fff;
		background-image: none;
		background-position: 0 90%;
		background-repeat: repeat no-repeat;
		background-size: 4px 3px;
		border-radius: 15px 225px 255px 15px 15px 255px 225px 15px;
		border-style: solid;
		border-width: 2px;
		box-shadow: rgba(0, 0, 0, .2) 15px 28px 25px -18px;
		box-sizing: border-box;
		color: #41403e;
		outline: none;
		transition: all 235ms ease-in-out;
		border-bottom-left-radius: 15px 255px;
		border-bottom-right-radius: 225px 15px;
		border-top-left-radius: 255px 15px;
		border-top-right-radius: 15px 225px;
		user-select: none;
		touch-action: manipulation;
	  }
	  
	  .button-7:hover {
		box-shadow: rgba(0, 0, 0, .3) 2px 8px 8px -5px;
		transform: translate3d(0, 2px, 0);
	  }
	  
	  .button-7:focus {
		box-shadow: rgba(0, 0, 0, .3) 2px 8px 4px -6px;
	  }
	
	
	  /* Button number 8  */
	
	.button-8 {
	  align-items: center;
	  background-color: #fee6e3;
	  border: 2px solid #111;
	  border-radius: 8px;
	  box-sizing: border-box;
	  color: #111;
	  cursor: pointer;
	  display: flex;
	  justify-content: center;
	  position: relative;
	}
	
	.button-8:after {
	  background-color: #111;
	  border-radius: 8px;
	  content: "";
	  display: block;
	  height: 48px;
	  left: 0;
	  width: 100%;
	  position: absolute;
	  top: -2px;
	  transform: translate(8px, 8px);
	  transition: transform .2s ease-out;
	  z-index: -1;
	}
	
	.button-8:hover:after {
	  transform: translate(0, 0);
	}
	
	.button-8:active {
	  background-color: #ffdeda;
	  outline: 0;
	}
	
	.button-8:hover {
	  outline: 0;
	}
	
	/* Button number 9 css here  */
	
	
	.button-9 {
	  all: unset;
	  background: transparent;
	  position: relative;
	  color: #f0f0f0;
	  z-index: 1;
	  cursor: pointer;
	  display: flex;
	  align-items: center;
	  justify-content: center;
	}
	
	.button-9::after,
	.button-9::before {
	  content: '';
	  position: absolute;
	  bottom: 0;
	  right: 0;
	  z-index: -99999;
	  transition: all .4s;
	}
	
	.button-9::before {
	  transform: translate(0%, 0%);
	  width: 100%;
	  height: 100%;
	  background: #28282d;
	  border-radius: 10px;
	}
	
	.button-9::after {
	  transform: translate(10px, 10px);
	  width: 35px;
	  height: 35px;
	  background: #ffffff15;
	  backdrop-filter: blur(5px);
	  -webkit-backdrop-filter: blur(5px);
	  border-radius: 50px;
	}
	
	.button-9:hover::before {
	  transform: translate(5%, 20%);
	  width: 110%;
	  height: 110%;
	}
	
	.button-9:hover::after {
	  border-radius: 10px;
	  transform: translate(0, 0);
	  width: 100%;
	  height: 100%;
	}
	
	.button-9:active::after {
	  transition: 0s;
	  transform: translate(0, 5%);
	}
	
	  
	/* button number 10  css here  */
	
	.button-10 {
	  text-transform: uppercase;
	  transition: 0.5s;
	  background-size: 200% auto;
	  box-shadow: 0px 0px 14px -7px #f09819;
	  background-image: linear-gradient(45deg, #FF512F 0%, #F09819  51%, #FF512F  100%);
	}
	
	.button-10:hover {
	  background-position: right center;
	  /* change the direction of the change here */
	  color: #fff;
	  text-decoration: none;
	}
	
	.button-10:active {
	  transform: scale(0.95);
	}
	
	
	/* button number 11 css here  */
	
	
	.button-11 {
	  --b: 3px;   /* border thickness */
	  --s: .45em; /* size of the corner */
	  --color: #373B44;
	  
	  padding: calc(.6em + var(--s)) calc(.9em + var(--s));
	  color: var(--color);
	  --_p: var(--s);
	  background:
		conic-gradient(from 90deg at var(--b) var(--b),#0000 90deg,var(--color) 0)
		var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
	  transition: .3s linear, color 0s, background-color 0s;
	  outline: var(--b) solid #0000;
	  outline-offset: .6em;
	}
	
	.button-11:hover,
	.button-11:focus-visible{
	  --_p: 0px;
	  outline-color: var(--color);
	  outline-offset: .05em;
	}
	
	.button-11:active {
	  background: var(--color);
	  color: #fff;
	}
	
	/* button number 12 & 13 css here  */
	
	
	/* CSS */
	.button-12,
	.button-13  {
	  position: relative;
	  overflow: hidden;
	  border: 1px solid #18181a;
	  color: #18181a;
	}
	.button-12:hover,
	.button-13:hover {
	  color: #fff;
	}
	
	.button-12 span,
	.button-13 span {
	  position: relative;
	  transition: color 600ms cubic-bezier(0.48, 0, 0.12, 1);
	  z-index: 10;
	}
	
	.button-12:after,
	.button-13:after {
	  content: "";
	  position: absolute;
	  bottom: -50%;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  background-color: black;
	  transform-origin: bottom center;
	  transition: transform 600ms cubic-bezier(0.48, 0, 0.12, 1);
	  transform: skewY(9.3deg) scaleY(0);
	}
	
	.button-12:after {
	  bottom: -50%;
	  transform-origin: bottom center;
	}
	
	.button-13:after {
	  bottom: 50%;
	  transform-origin: top center;
	}
	
	.button-12:hover:after,
	.button-13:hover:after {
	  transform: skewY(9.3deg) scaleY(2);
	}
	
	.button-12:hover:after {
	  transform-origin: bottom center;
	}
	
	.button-13:hover:after {
	  transform-origin: top center;
	}
	
	
	/* button number 14 css here  */
	
	/* CSS */
	.button-14 {
	  /* appearance: none;*/
	  background-color: #000000; 
	  border: 2px solid #1A1A1A;
	  color: #FFFFFF;
	  margin: 0;
	  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
	}
	
	.button-14:disabled {
	  pointer-events: none;
	}
	
	.button-14:hover {
	  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
	  transform: translateY(-2px);
	}
	
	.button-14:active {
	  box-shadow: none;
	  transform: translateY(0);
	}
	
	/* button number 15 css here  */
	
	/* CSS */
	.button-15 {
	  --c: #fff;
	  /* text color */
	  background: linear-gradient(90deg, #0000 33%, #fff5, #0000 67%) var(--_p,100%)/300% no-repeat,
		#004dff;
	  /* background color */
	  color: #0000;
	  border: none;
	  transform: perspective(500px) rotateY(calc(20deg*var(--_i,-1)));
	  text-shadow: calc(var(--_i,-1)* 0.08em) -.01em 0   var(--c),
		calc(var(--_i,-1)*-0.08em)  .01em 2px #0004;
	  outline-offset: .1em;
	  transition: 0.3s;
	}
	
	.button-15:hover,
	.button-15:focus-visible {
	  --_p: 0%;
	  --_i: 1;
	}
	
}
